@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Font Libaray */
* {
  box-sizing: border-box;
  font-family: 'Poppins';
  font-weight: 700px;
  font-style: normal;
}
.App {
  text-align: center;
}

/***************************** HEADER *********************************************/
.App-header {
  /* 
    I Was thinking it would be cool to have a photo of a victoria view
    Could also make this a  bootstrap carousel.
  */
  background-image: url(/static/media/boat.a9266307.jpg);
  background-position:center top;
  background-size: 100%;
  width: 100%;
  align-items: center; 
  justify-content: center; 
  /* font-size: calc(10px + 2vmin); */
  color:#2c59a0;
  padding: 7px;
}
.Header-text h1{
  font-weight: bold;
}
.Header-text{
  font-size:1.8vw;
  font-weight: regular;
  text-shadow:0px 5px 10px #ffffff;
  line-height: 80%;

  /* 
  font-family: 'Exo', sans-serif;
  font-family: 'Poppins', sans-serif;
  font-family: 'Quicksand', sans-serif; 
  font-family: 'Raleway', sans-serif;
  font-family: 'Titillium Web', sans-serif; 
  */
}

.jumbo {
  width: 99%;
  align-items: center; 
  justify-content: center; 
  background:transparent  !important;
  margin: 20px auto auto auto;
}

/*************** LOGOS ********/
.Logos{
  background-color: white;
  width: 50%;
  margin: auto;
  box-shadow: 0px 3px 8px 1px #0C0343;
  border-radius: 5px;
}

.Checkfront-logo{
  width: 50%;
  padding: 10px;
  background-color: white;
}
.Camosun-logo{
  width: 50%;
  padding: 10px;
  background-color: white;
}
/**************** logo end *****************/


.App-link {
  color: #61dafb;
}


@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




.nav{
  background-color:#E9EDEE;
}


/* The whole app*/
* {
  margin: 0;
  padding: 0;
}


/* //////////////////////////////////// FOOOTER /////////////////////////// */

/* all of the footer */
.footer {
  background-color:#366fc9;
  color: rgb(0, 0, 0);
  text-align: left;
  color: white;
  position: relative;
  top: 175px;
  bottom:0;
  left: 0;
  right: 0;
  padding-top: 15px;
  padding-bottom: 5px;
  
}

/* ///////////////////////////////////// LISTS ////////////////////////// */

/* all lists */
ul{
  list-style: none;
}

/* ///////////////////////////////////// LINKS ///////////////////////// */
/* link tags */
a:link {
  color: #a4bbe0;
  background-color: transparent;
  text-decoration: none;
}

a:hover {
  color: #a59bf3 !important;
  text-decoration: none !important;
}

/* vistited links */
a:visited {
  color:#a4bbe0;
  background-color: transparent;
  text-decoration: none;
}


/* ///////////////////////////////// TOP BUTTON ///////////////////// */
/* Return to the top of the page button */
.top{
 
  margin-top: 5px;
  box-shadow: 0px 1px 8px 1px #534e7a;
  text-shadow: darkgrey;

}

/* When the top of page button has been clicked  */
.top:hover{
  background-color: #c8cddb !important;
}

.territory p{
  color: #c9c5f5 !important;
  font-size: x-small;
  text-align: justify;
  padding-right: 15%;
}

/* /////////////////////////////////// COPYRIGHT ///////////////////// */
/* Copyright part of the footer */
.footer-copyright{
  text-align: center;
}


.desc {
  margin: 20px 0px 20px 0px;
  text-align: left;
  display: block;
}

.homePicture{
  max-width: 95%;
  max-height: 95%;
 
}

.buzzword{
  color: #366fc9;
  font-weight: bold;
  font-size: xx-large; 
  text-align: center;
}



.icon {
  color: white !important;
  margin: 30px 10px 0px 10px;
  
}

.icon:hover {
  color: #d0d0d0da !important;
}

.photo {
  margin: 20px auto auto auto;

}

.p{
  text-align: center;
  padding: 3%;
}
.document {
  align-self: center;
  margin: 0px auto 0 auto;
}

canvas {
  align-self: center;
  margin: 0 auto 0 auto;
}


link {
  margin: 5px auto 100px auto;
}
.space {
  margin: 160px auto auto auto;
}
@media all and (min-width: 480px) {
  .Login {
